import './Carrito.css'

import {observer} from 'mobx-react-lite'
import React, {useEffect, useRef, useState} from 'react'
import * as FIcon from 'react-feather'
import {Link as SubheaderLink, SubHeader} from '../../components/SubHeader'
import {CheckoutCarrito, Page} from '../../components'
import {RUTAS} from '../../constants'
import {formatCantidadCarrito, plata_fmt} from '../../helpers'
import {DetailViewProps, formatTextoURL, useConfirm, useToaster} from '../../helpers-ts'
import {useStores} from '../../models/root-store/root-store-context'
import {ProductoCarrito} from '../../models/types/ProductoCarrito'
import {useHistory} from 'react-router'
import {Instance} from 'mobx-state-tree'
import {MdAddCircleOutline, MdRemoveCircleOutline} from 'react-icons/md'
import {BsTrash} from 'react-icons/bs'

export const Carrito: React.FC<DetailViewProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore, carritoStore, adminStore } = rootStore
  const Movil = window.innerWidth <= 500 || /Mobi/.test(navigator.userAgent)
  const [LinksSubHeader, setLinksSubHeader] = useState<SubheaderLink[]>()
  const pageRef = useRef(null as any)
  const [Index, setIndex] = useState<'metodo-envio' | 'tarjeta-direcion'>('metodo-envio')

  React.useEffect(() => {
    if (loginStore.isLoggedIn) {
      obtenerDatosCliente()
      obtenerCarrito()
    }
  }, [])

  useEffect(() => {
    if (Index === 'metodo-envio') {
      setLinksSubHeader([{ titulo: 'Carrito', callback: () => setIndex('metodo-envio') }])
    } else {
      setLinksSubHeader([{ titulo: 'Carrito', callback: () => setIndex('metodo-envio') }, { titulo: 'Checkout' }])
    }
  }, [Index])

  const obtenerCarrito = () => {
    rootStore.environment.api.carrito().then((result) => {
      if (result.kind === 'ok') {
        carritoStore.setProductosAPI(result.data)
      }
    })
  }

  const obtenerDatosCliente = () => {
    rootStore.environment.api.getCliente(loginStore.id).then((result) => {
      if (result.kind === 'ok') {
        const cliente = result.cliente as any
        loginStore.setDirecciones(cliente.direcciones)
        loginStore.setDatosFacturacion(cliente.datos_facturacion)
      }
    })
  }

  return (
    <Page ref={pageRef} login_required={false} solo_cliente={true}>
      {/* Contenido de la página*/}
      {LinksSubHeader && <SubHeader links={LinksSubHeader} />}
      {Movil && (
        <div className="d-flex" style={{ gap: 10, fontSize: 12, marginTop: 10, marginLeft: 10 }}>
          <a onClick={() => history.goBack()}>
            <FIcon.ArrowLeft size={Movil ? 15 : 20} />
          </a>

          <div>
            <span>Seguir Comprando</span>
          </div>
        </div>
      )}
      <div className="container-lg">
        {Index === 'metodo-envio' && (
          <div className="container-checkout-carrito" style={{ display: 'flex' }}>
            {carritoStore.productos.length > 0 && (
              <div style={{ flex: 1 }}>
                <CheckoutCarrito index={Index} setIndexCallback={(index: any) => {
                  pageRef.current.scrollTop()
                  setIndex(index)
                }} />
              </div>
            )}
            <div className={`p-0 background-white items-carrito-container ${carritoStore.productos.length < 1 ? 'border-0' : ''}`} style={{ flex: 1 }}>
              <div className={'row mx-0'}>
                {carritoStore.productos.length < 1 && (
                  <div className={'col-12 text-center my-5 py-5 '}>
                    <p>
                      <h3>Tu carrito está vacío</h3>
                    </p>
                    <p>Tu carrito aún no tiene productos</p>
                    <button className={'btn btn-info'} onClick={() => history.push('/')}>
                      <span>Comprar ahora</span>
                    </button>
                  </div>
                )}
                {carritoStore.productos.length > 0 && carritoStore.productos.map((producto, index) => (
                  <RenderProducto key={index} movil={Movil} productoCarrito={producto} />
                ))}
              </div>
            </div>
          </div>
        )}
        {Index === 'tarjeta-direcion' && (
          <div className="" style={{ display: 'flex' }}>
            {carritoStore.productos.length > 0 && (
              <CheckoutCarrito index={Index} setIndexCallback={(index: any) => setIndex(index)} />
            )}
          </div>
        )}
      </div>
    </Page>
  )
})

const RenderProducto: React.FC<{ productoCarrito: Instance<typeof ProductoCarrito>; movil: boolean }> = observer(
  (props) => {
    const history = useHistory()
    const rootStore = useStores()
    const { carritoStore, loginStore } = rootStore
    const productoCarrito = props.productoCarrito
    const [CantidadCarrito, setCantidadCarrito] = useState(productoCarrito.cantidad)

    const stNombre: React.CSSProperties = { fontSize: 14, fontWeight: 'bolder', color: 'var(--gris-oscuro-abi)' }
    const stCantidades: React.CSSProperties = { fontSize: 12, fontWeight: 'bolder', color: 'var(--gris-oscuro-abi)' }
    const stPrecio: React.CSSProperties = { fontSize: 10, fontWeight: 'bold', color: 'var(--gris-oscuro-abi)' }
    const Movil = props.movil
    const [Loading, setLoading] = useState(false)
    const addToast = useToaster()

    const deleteProductoCarrito = (producto: any) => {
      if (!Loading) {
        mostrarDialogo('Esta seguro que desea borrar el producto?', producto)
      }
    }

    const [mostrarDialogo, ConfirmDialog] = useConfirm((producto: any) => {
      if (loginStore.isLoggedIn) {
        setLoading(true)
        rootStore.environment.api
          .agregarEliminarProductoCarrito(producto.id, undefined, undefined, true)
          .then((result) => {
            setLoading(false)
            if (result.kind === 'ok') {
              carritoStore.removeProducto(producto)
            }
          })
      } else {
        carritoStore.removeProducto(producto)
      }
      addToast('Producto eliminado del carrito', 'success')
    })

    const addProductoCarrito = (producto: any) => {
      if (!Loading) {
        if (loginStore.isLoggedIn) {
          setLoading(true)
          rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, 1).then((result) => {
            setLoading(false)
            if (result.kind === 'ok') {
              carritoStore.sumarProducto(producto)
            }
          })
        } else {
          carritoStore.sumarProducto(producto)
        }
      }
    }

    const minusProductoCarrito = (producto: any) => {
      if (!Loading) {
        if (producto.cantidad < 2) {
          deleteProductoCarrito(producto)
          return
        }
        if (loginStore.isLoggedIn) {
          setLoading(true)
          rootStore.environment.api.agregarEliminarProductoCarrito(producto.id, null, -1).then((result) => {
            setLoading(false)
            if (result.kind === 'ok') {
              carritoStore.restarProducto(producto)
            }
          })
        } else {
          carritoStore.restarProducto(producto)
        }
      }
    }

    useEffect(() => {
      setCantidadCarrito(productoCarrito.cantidad)
    }, [productoCarrito.cantidad])

    return (
      <>
        {ConfirmDialog}
        <div className="producto-carrito-container">
          <div className="imagen-producto-container">
            <img
              className="imagen-producto-carrito"
              src={productoCarrito.imagenes ? productoCarrito.imagenes : 'assets/abi-logo-producto.png'}
            />
          </div>
          <div style={{ flex: 3, display: 'flex', flexDirection: 'column', justifyContent: 'space-between' }}>
            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
              <div>
                <p className="nombre-producto-carrito titulo-nombre-producto-carrito">{productoCarrito.nombre}</p>

                <div
                  className={'cpointer'}
                  onClick={() => history.push(RUTAS.PRODUCTO_DETAIL(productoCarrito.id, formatTextoURL(productoCarrito.nombre)))}
                >
                  <span style={{ color: 'var(--naranja-abi)', fontSize: '0.875rem' }}>
                    Ver detalles
                  </span>
                </div>
              </div>
              <button style={{ backgroundColor: '#fff' }} onClick={() => deleteProductoCarrito(productoCarrito)}>
                <BsTrash size={Movil ? 20 : 30} className="icono-remove" />
              </button>
            </div>
            <div className="d-flex pr-2 pr-lg-0" style={{ justifyContent: 'space-between' }}>
              <div>
                <div>
                  <span>Precio unitario: {plata_fmt(productoCarrito.precio)}</span>
                </div>
                <div>
                  <span style={{ fontWeight: 'bold' }}>
                    Precio total: {plata_fmt(productoCarrito.precio * productoCarrito.cantidad)}
                  </span>
                </div>
              </div>
              <div className="d-flex" style={{ alignItems: 'center', gap: Movil ? 5 : 30 }}>
                <div onClick={() => minusProductoCarrito(productoCarrito)}>
                  <MdRemoveCircleOutline size={!Movil ? 30 : 20} className="icono-remove" />
                </div>

                <span
                  style={{
                    color: 'var(--gris-claro-abi)',
                    fontWeight: 'bold',
                    fontSize: '1.2rem',
                  }}
                >
                  {formatCantidadCarrito(CantidadCarrito)}
                </span>

                <div onClick={() => addProductoCarrito(productoCarrito)}>
                  <MdAddCircleOutline size={!Movil ? 30 : 20} className="icono-add" />
                </div>
              </div>
            </div>
          </div>
        </div>
      </>
    )
  },
)

export default Carrito
